@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
/* Imports */

body {
  font-family: "Roboto Condensed";
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


/* *********************************** */
  /* Styles for our navigation bar */
/* *********************************** */

/* For picture */
.brand {
    width: 5rem;
    /* height: */
}

/* Background */
.navbar {
    background-color: #343a40;
}


/*  For Links */
nav a.nav-link {
    font-size: 1rem;
    font-weight: 200;
    letter-spacing: 0.0375rem;
    cursor: pointer;
    color: #fff !important;
}

nav ul li {
    text-transform: uppercase;
}

.navbar-nav > li > a {
    margin-right: 1.25rem;
    margin-left: 1.25rem;
    border-top: 0.1875rem solid #343a40;
    color: #343a40;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:hover  {
    border-top: 0.1875rem solid #e7c9a9;
    /* border-bottom: 0.1875rem solid #e7c9a9; */
}

/* Logo */
.navbar-brand {
    max-width: 17.25rem;
    padding : 0 !important;
    height: 6rem !important;
}

/* *********************************** */
  /* Styles for our home component */
/* *********************************** */

.wrapper {
    position: relative;
    /* background: url(../../images/back1.jpeg) no-repeat; */
    background-size: cover;
    background-position: center;
    height: 90vh;
}

.items {
    display: -webkit-flex;
    display: flex;
    position: absolute; /* So I can place particles on top */
    -webkit-flex-direction: column;
            flex-direction: column; /* Column because we want to layout in column */
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;

    /* Center */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
}

.header_info {
    color: white;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 5rem;
}

.typed {
    font-size:  2rem;
    color: #fff;
    letter-spacing: 5px;
}

.btn-contact{
    width: 10rem;
    height: 4rem;
    border: 5px solid #e7c9a9;
    background-color: seashell;
    text-transform: uppercase;
    padding: 0.625rem 0.75rem 0.75rem 0.75rem;
    margin: 2rem;
}

.btn-contact:hover {
    text-decoration: none;
    background-color: lightcoral;
    transition : .2s ease-in-out;
}

.tsparticles-canvas-el {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 3;
}

/* RESPONSIVE FOR SMALLER SCREENS */

@media(max-width: 768px) {
    /* Making the  Heading Smaller */
    .btn-contact {
        width: 7.5rem;
        height: 3rem;
    }
    .typed {
        font-size : 1rem !important;
    }
    .header_info {
        letter-spacing: 1px;
        font-size: 1.7rem;
    }
}


/* Extra */

.carousel, .carousel-inner, .carousel-item {
    position: absolute;
    /* display:block !important; */
    height: 90vh;
    width: 100%;
    /* z-index: 1; */
    /* bottom:0px; */
}

.carousel-control-prev, .carousel-control-next, .carousel-indicators {
    z-index: 4 !important;
}

.carousel-inner > .carousel-item > img {
    width: 100%;
    height: 100%; 
    background: no-repeat;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}


/* *********************************** */
  /* Styles for the about component */
/* *********************************** */

/* Picture */
.about {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.about > img {
    /* width: 90%;
    height: 100%; */
    width: 24rem;
    padding: 0.3rem;
    border: 1px solid #e7c9a9;
}

.about_text > h1 {
    color: #e7c9a9;
    text-transform: uppercase;
}

.about_text > p {
    font-size: 12pt;
}

.pdfs {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.icon {
    width: 10rem;
}

@media(max-width : 468px) {
    .about > img {
        width: 10rem;
    }
}


/* *********************************** */
  /* Styles for the Portfolio component */
/* *********************************** */

/*  PORTFOLIO PROJECT */

#Portfolio {
    background : white;
    padding: 3rem 0;
}

#Portfolio > .container-md {
    padding-bottom: 6.5rem !important;
}

.portfolio-wrapper {
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.portfolio-holder {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    margin : 1rem;
}

.proj-image, .selecting {
    height: 12rem !important;
    width: 17rem !important;
    border: 1px solid #e7c9a9;
    padding: 0.6rem;
}

.portfolio-holder:hover .portfolio-icon1, .portfolio-holder:hover .portfolio-icon2 {
    opacity: .70 !important;
    transition: 0.4s ease-in-out !important;
    cursor: pointer !important;
}

.selecting {
    position: absolute;
    opacity : 0.0;
    background : black;
    top : 0;
    border: 4px solid #e7c9a9;
    cursor: pointer;
    z-index: 1;
}

.portfolio-holder:hover .selecting {
    opacity: .55 !important;
    transition: 0.4s ease-in-out !important;
}

.portfolio-icon1 {
    position: absolute;
    opacity: 0;
    margin-left: -5rem;
    color: white;
    font-size: 2.7rem;
    z-index: 2
}
.portfolio-icon2 {
    position: absolute;
    opacity: 0;
    margin-left: 5rem;
    color: white;
    font-size: 2.7rem;
    z-index:2
}

/* POPUP */

.popup-title {
    letter-spacing: 3px;
}

.pop-up {
    width: 45rem;
    padding: 0 0.5rem;
}

.github {
    cursor: pointer;
}

.github:hover {
    color: darkblue;
}

/* Media Queries */

@media(max-width: 768px) {
    .pop-up {
        width: 100%;
    }

}
/* *********************************** */
  /* Styles for the Contact component */
/* *********************************** */

.contact {
    background : #343a40;
    /* padding : 3rem 0; */
}

.header {
    color: #e7c9a9;
    padding: 1.3rem 0;
}

.subheader {
    color: white;
    font-size: 1.2rem;
}

.contact input, .contact textarea {

    /* Sizing */  
    width: 100%;
    top: 0;
    margin-bottom: 1rem;


    /* Border */
    border: 0;
    border-bottom: 0.125rem solid darkgray;
    border-radius: 0;

    background: none;
    color: white;
    box-shadow: none !important;
}

.contact textarea {
    height: 12.5rem;
    resize: none;
}

.contact, .container {
    padding: 4.2rem;
}

.contact input:focus, .contact textarea:focus {
    background: transparent;
    color: #e7c9a9;
    border-bottom: 0.125rem solid #e7c9a9;
}


.submit-btn {

    /* Sizing */
    height: 3rem;
    width: 100%;

    /* Styling */
    background-color: darkslategray !important;
    border: 2px solid #656565;
    margin-top: 1.2rem;
    background: none;
}

.msg {
    padding-top: 1rem;
    color: red;
}

/* Media Queries */

@media(max-width: 768px) {
    .contact .container {
        padding: 0.8rem;
    }
}

/*  */
.footer {
    background: #2a2e33;
    color: white;
